@import "../../variables";

.mainPage {
    display: flex;
    height: 100vh;
    padding-left: 64px;

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        padding: 32px 64px 46px;
        width: 50%; }

    &__title {
        color: $main_black;
        font-weight: 700;
        font-size: 70px; }

    &__subtitle {
        color: $main_accent;
        font-weight: 700;
        font-size: 70px; }
    &__description {
        color: $main_gray;
        margin-top: 34px;
        margin-bottom: 60px;
        font-weight: 300;
        font-size: 26px; }
    &__content {
        padding-bottom: 101px; } }

.slide {
    &__background {
        height: 100%;
        position: relative;
        mix-blend-mode: normal;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;

        &::after {
            content: "";
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%); } }

    &__content {
        position: relative;
        z-index: 1;
        color: $main_white;
        padding: 237px 96px;
        box-sizing: border-box; }

    &__title {
        font-weight: 500;
        font-size: 40px;
        line-height: 47px;
        margin-bottom: 8px; }
    &__description {
        font-weight: 300;
        font-size: 24px;
        line-height: 100%;
        color: $main_gray_light;
        margin-bottom: 32px; } }

.slider__container {
    width: 50%; }

@media ( max-width: 1439px) {
    .mainPage {
        &__container {
            padding: 32px 32px 46px;
            width: 60%; } }
    .slider__container {
        width: 40%; }
    .slide {
        &__content {
            padding: 277px 64px; }
        &__title {
            font-size: 32px;
            line-height: 37px; }
        &__description {
            font-size: 22px;
            line-height: 100%; } } }

@media ( max-width:  1023px) {
    .mainPage {
        &__container {
            width: 100%; } } }

@media ( max-width:  767px) {
    .mainPage {
        padding: 0;

        &__container {
            padding: 16px 0 0; }
        &__title {
            font-size: 32px;
            line-height: 100%;
            margin-left: 16px; }
        &__subtitle {
            font-size: 32px;
            line-height: 100%;
            margin-left: 16px; }
        &__description {
            font-size: 18px;
            line-height: 21px;
            margin: 16px 16px 32px; } } }
